<template>


  <v-layout column d-flex :class="mainClass">
    <v-flex v-if="started" class=" align-center " d-flex :style="style.item0">
      <v-container class="pa-0 d-flex"  >

        <v-progress-circular  :value="time_to_change" class="mx-2"></v-progress-circular>
        <v-slide-x-reverse-transition>
          
          <span class="d-flex justify-center" v-show="show" style="font-size: 2rem">
            {{department}}
          </span>
        </v-slide-x-reverse-transition>
        <v-spacer></v-spacer>
        <v-menu :close-on-content-click="false" transition="scale-transition"
          min-width="325px">
          <template v-slot:activator="{ attrs, on }">
            <v-input prepend-icon="mdi-calendar" class="primary--text" width="325px" hide-details  v-bind="attrs" v-on="on" style="font-size: 1.5rem">{{formatedDate}}</v-input>
          </template>
            <v-date-picker v-model="dates" range ></v-date-picker>
        </v-menu>
        
        <v-switch v-model="$vuetify.theme.dark" class="ma-0"></v-switch>  

      </v-container>
    </v-flex>
    <v-flex v-if="started" d-flex :style="style.item1">
       <v-container class="pa-0 d-flex">
          <v-layout >
          
            
            <v-flex  d-flex v-for="(item, index) in topThree" :key="index"  >
              <v-slide-y-transition >
               <span  :style="{width: '100%', height: '100%', margin:'0 2%'}" v-if="show" class="pa-p10">
 
                <v-sheet :class="['flex elevation-2 d-flex flex-column full-height', { zoom: index == 1, 'zoom-out': index == 0  }] " >
                  
                  <v-row class="pa-0 mx-0 text-center align-center flex" v-if="item">
                    
                    <v-col cols="3" class="py-0 font-weight-bold">
                      <v-chip style="font-size: 1.2vw; border: solid 2.5px; background: transparent" >{{ place[index]}}</v-chip>
                    </v-col>
                    <v-col class="pa-0 text-center font-weight-bold" style="font-size: 1.3
                    vw;">
                      <span v-if="item && item.user" class="fs-2-rem">
                        {{ item.user.displayName }}
                      </span>
                    </v-col>
                    <v-col cols="3" class="py-0">
                      <span style="font-size: 2rem;" >{{ badge[index]}}</span>
                    </v-col>
                  </v-row>
                  <v-divider></v-divider>
                  <v-row class="pa-0 mx-0 text-center align-center flex">
                    <v-col cols="12" class="py-0 " >
                      <v-avatar class="mx-auto " size="23rem"  v-if="item && item.user" >
                        <v-img :src="getImageSrc(item.user.profileImage)"></v-img>
                      </v-avatar>
                      <v-alert icon="mdi-alert" style="max-width: 200px" class="ma-0 mx-auto" v-else>
                        No Data Avilable !
                      </v-alert>
                    </v-col>
                  </v-row>
                  
                  <v-row class="pa-0 mx-0 text-center align-center flex " style="font-size: 1.1vw" v-if="item">
                    <v-col cols="12" class="d-flex align-end ">
                      <v-row class=" mx-0 text-center">
                        <v-col cols="3" class="pa-0 font-weight-bold d-flex flex-column text-center" style="font-size: 2rem">
                          <span class="caption flex" :style="style.item0">Deposits</span>
                          <span color="primary lighten-2 grow" v-if="item && item.count">{{ item.count}}</span>
                        </v-col>
                        <v-col class="pa-0 text-center  d-flex  flex-column" style="font-size: 2rem">
                          <span class="caption flex">Total</span>

                          <span class="green--text flex grow" v-if="item && item.amount">{{ utility.money(item.amount)  }}</span>
                          
                        </v-col>
              
                        <v-col cols="3" class="py-0 d-flex flex-column text-center" style="font-size: 2rem">
                          <span class="caption flex">Achievement</span>
                          <span v-if="item && item.percentage" class="flex" :style="[getColor(item.percentage), 'font-size: 1.1vw']">
                            {{ item.percentage ? item.percentage.toFixed(2) : 0 }}%
                          </span>
                        </v-col>
                        
                      </v-row>
                    </v-col>
                  </v-row>
                </v-sheet>
               </span>
              </v-slide-y-transition>

            </v-flex>
            
          </v-layout>
       </v-container> 
    </v-flex>
    <v-flex :class="{
      'primary lighten-2': !$vuetify.theme.dark,
      'grey darken-2': $vuetify.theme.dark,
      'justify-center': true
    }" d-flex :style="style.item2">
      <v-container class="pa-0" v-if="started">
        <v-list color="transparent" class="py-0">
            <v-list-item :class="['my-0','py-0']"> 
              <v-list-item-icon class="mx-1 white--text font-weight-bold py-0">
                #
              </v-list-item-icon>
              <v-list-item-avatar class="mx-2 my-0 py-0">
                
              </v-list-item-avatar>
              <v-list-item-content>
                <v-row class="py-0 white--text agents-header">
                  <v-col cols="6" class="py-0">
                    Agent
                  </v-col>
                  <v-col class="py-0 " >
                    Deposits
                  </v-col>
                  <v-col cols="2"  class="py-0  text-center ">
                    Total
                  </v-col>
                
                  <v-col class="py-0  d-flex  align-end flex-column"  >
                    <span  >
                      Achievement
                    </span>
                  </v-col>
                </v-row>
              </v-list-item-content>
            </v-list-item>
        </v-list>
        <v-list color="transparent" class="pa-0"> 
      

          <v-slide-y-reverse-transition v-if="!restOfUser.length">
          <v-list-item  v-show="show"  :class="['my-2', 'v-sheet', {'theme--dark': isDark}]"> 
            <v-alert icon="mdi-alert" class="ma-0" >
              No Data Avilable !
            </v-alert>
          </v-list-item>
          </v-slide-y-reverse-transition>
          <v-slide-y-reverse-transition v-for="(item, index) in restOfUser" :key="index">
          <v-list-item  v-show="show"  :class="['my-2', 'v-sheet', {'theme--dark': isDark}]"> 
            <v-list-item-icon class="mx-1 font-weight-bold">
              <v-chip style=" border: solid 1.5px; background: transparent; font-size: 1.5rem"  >{{index + 4}}</v-chip>
              
              
            </v-list-item-icon>
            <v-list-item-avatar class="mx-2" size="4rem" >
              <v-img :src="getImageSrc(item.user.profileImage)" style="position: absolute" ></v-img>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-row class="py-0">
                <v-col cols="6" class="py-0" style="font-size: 1.8rem">
                  {{item.user.displayName}} 
                </v-col>
                <v-col class="py-0 font-weight-bold" >
                  <span color="primary lighten-2" style="font-size: 1.8rem">  {{ item.count}} </span>
                </v-col>
                <v-col cols="2"  class="py-0 green--text text-center " style="font-size: 1.8rem">

                  {{ utility.money(item.amount)  }}
                   
                </v-col>
              
                <v-col class="py-0  d-flex  align-end flex-column" :style="[getColor(item.percentage), { fontSize: '1.8rem'}]" >
                  <span>

                    {{ item.percentage ? item.percentage.toFixed(2) : 0 }}%
                  </span>
                </v-col>
              </v-row>
              
            </v-list-item-content>
          
          </v-list-item>
          </v-slide-y-reverse-transition>
        </v-list>
      </v-container>
       <v-flex v-else class="align-self-center text-center" style="max-width: 30%">
         <v-btn x-large @click="start"> Click here to start </v-btn>
       </v-flex>
    </v-flex>
     
    <v-dialog v-model="dialog" fullscreen>
      <v-card v-if="data">
        <v-progress-linear :value="data.loader * 100 / qeueu.dialog"></v-progress-linear>
        <v-card-title >
            <div class=" text-center d-flex align-center">
                <country-flag :country="data.item.customer.country" size="big" rounded ></country-flag>
                {{ data.item.customer.country | countryName}}
              </div>
            <v-spacer></v-spacer>
            <v-icon>mdi-calendar-month</v-icon>
            {{ data.dateRange[0] | date }} -  {{ data.dateRange[1] | date }}
        </v-card-title>
        <v-card-text  >
          <v-row style="height: 100%">
            <v-col cols="4" class="text-center" id="user">
              <v-avatar size="54vh" class="mx-auto">
                <v-img :src="getImageSrc(data.user.profileImage)"></v-img>
              </v-avatar>
              <h1 class="mx-auto display-size">{{ data.user.displayName }} {{ badge[data.user.position-1] }}</h1> 
              <table style="width: 80%" class="mx-auto">
              <tbody>
                <tr>
                  <td class="font-weight-bold">Desk</td>
                  <td>{{data.user.desk}}</td>
                </tr>
                <tr>
                  <td class="font-weight-bold">Department</td>
                  <td>{{data.user.department}}</td>
                </tr>
                <tr>
                  <td class="font-weight-bold">Target</td>
                  <td>{{utility.money(data.user.target)}} </td>
                </tr>
                <tr>
                  <td class="font-weight-bold">Total <small>(Deposits)</small> </td>
                  <td>{{utility.money(data.user.total)}} <small>({{ data.user.deposits }})</small></td>
                </tr>
                <tr>
                  <td class="font-weight-bold">Achievement</td>
                  <td>{{data.user.percentage ? data.user.percentage.toFixed(2) : 0}}%</td>
                </tr>
              </tbody>
              </table>     
            </v-col>
            <v-divider vertical></v-divider>
            <v-col cols="8" class="text-center d-flex flex-column" style="flex: 0 0 66.5%">
              <div style="width: 100%; flex:0.4">
                <p class="deposit-title">
                  ðŸŽ‰New ( <span class="deposit-title font-weight-bold">
                          {{ data.item.department.name }}
                        </span> ) DepositðŸŽ‰  
                </p> 
              </div>
          
              <div style="width: 100%; flex:0.3">
                <p class="green--text font-weight-bold money-title">{{ utility.money(data.item.amount)  }}</p>
              </div>
              <div  style="width: 100%; flex:0.3 d-flex align-center" class="deposit-title">
                 {{ data.item.affiliate.name }}
              </div>
            
              <div style="width: 100%; flex:0.3" class="d-flex">
                <table width="100%"  class="flex" style="align-self:flex-end" >
                  <tr>
                    <th>Desk</th>
                    <th>Desk Target</th>
                    <th>Desk Total <small>(Deposits)</small></th>
                    <th>Desk Achievement</th>
                    <th>{{data.user.displayName}} from Desk </th>
                  </tr>
                  <tr>
                    <td>
                      {{ data.desk.name }}
                    </td>
                    <td>
                      {{ utility.money(data.desk.target) }}
                    </td>
                    <td>
                      {{ utility.money(data.desk.total)  }} <small>({{ data.desk.deposits }})</small>
                    </td>
                    <td>
                      {{ data.desk.percentage ? data.desk.percentage.toFixed(2) : 0}}%
                    </td>
                    <td>
                      {{ data.desk.userPercentage ?  data.desk.userPercentage.toFixed(2) : 0 }}%
                    </td>
                  </tr>
                </table>
              </div>
           
            </v-col>


          </v-row>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout> 
    
</template>

<script>
import utility from './../utility'
import dateformt from 'dateformat'
var audio = new Audio('/Ding-ding-sound.mp3');

const style = {
  item0:{
    flex: '1 1 10%',
    maxHeight: '64px'
  },
  item1:{
    flex: '1 1 calc(65% - 64px)',
    height: 'calc(65% - 64px)'

  },
  item2:{
    flex: '1 1 35%',
     height: '35%'
  }
}
export default {
  name: 'Screen',

  data() {
    return {
      data: null,
      qeueu: {
        started: false,
        items: [],
        dialog: 30/*dialog times in second */
      },
      started: false,
      utility,
      style,
      timers:{ interval: null, count: 0, change: 10/*change tabs times in second */ },
      top_users:  [/*ftd*/[],/*rtn*/[]],
      tab:0,
      dialog: false,
    //  dates: ["2020-12-01", "2020-12-31"] //utility.currentDateRange
      dates: utility.currentDateRange
    }
  },
  watch: {
    async 'qeueu.started' (started) {
      console.log('Watch started!!!!!!!!!!', started);
      if (started) {

          while (this.qeueu.items.length) {
            const item = this.qeueu.items.shift()
            item.loader = this.qeueu.dialog-1
            this.data = item
            this.dialog = true
            const interval = setInterval(() => this.data.loader-- , 1000)

            audio.play()
            await new Promise(resolve => {
              setTimeout(() => {
                this.dialog = false
                audio.pause();
                audio.currentTime = 0;
                clearInterval(interval)
                setTimeout(resolve, 1000) // wait animation
              }, this.qeueu.dialog * 1000)
            })
            if (!this.qeueu.items.length)
              this.qeueu.started = false
          }
          
      }
    },
    dates (dates) {
      console.log(dates.length);
      if (dates.length > 1) this.fetch()
    }
  },
  computed: {
    io () {
      return this.$store.state.api.io
    },
    show() {
      return this.time_to_change > 0
    },
    place () {
      return  [3,1,2] 
    },
     badge () {
      return  ['ðŸ¥‰', 'ðŸ‘‘',  'ðŸ¥ˆ'] 
    },
    users () {
      return this.top_users[this.tab]
    },
    department () {
      const dps = ['Sales', 'Retention']
      return dps[this.tab]
    },
    topThree () {
        let topUsers = this.users.filter((e, i) => e && i <3)
        const users = new Array(3) 
        users[1] = topUsers[0]
        users[0] = topUsers[2]
        users[2] = topUsers[1]
        // const therdPlace = topUsers.pop()
        // topUsers.unshift(therdPlace)
        // console.log(users);
        return users 
    },
    hasTopThree () {
      return this.topThree.filter(e => e).length > 0
    },
    restOfUser () {
      const t = this.users.slice(3)
      return t 
    },
    formatedDate () {
      return  this.dates.map( e => dateformt(e, 'dd/mm/yyyy')).join(' - ')
    },
    time_to_change () {
      const x = 100 / this.timers.change
      const y = (100 / (this.timers.change-1)) / x
      const _100 = 100*y
      const percentage = this.timers.count *  _100 / this.timers.change
      return percentage
    }
  },
 
  methods: {
    
    async start () {
      console.log('Started listening');
      this.started = true
      this.io.on('notefy', this.notefy)

      await this.fetch()
      this.timers.interval = setInterval(() => {
        this.timers.count++;
        // this.init_time = new Date()
        if (!(this.timers.count%this.timers.change)) {
          // delay
          setTimeout(() => {
            this.tab = this.tab ? 0 : 1
          }, 1000)
            this.timers.count=0
        }
      }, 1000 )
    },
    stop () {
      this.started = false
      console.log('stoped listening');
      this.io.off('notefy', this.notefy)
    },
    async notefy (data) { 
      await this.fetch(data)
       
      this.qeueu.items.push(data)

      if (!this.qeueu.started)
        this.qeueu.started = true
      
      
    
     
    },
    getColor (percentage) {
         const color = utility.findColorBetween({ r:255, g:0, b:0 }, { r:0, g:200, b:0 },percentage)
        return { color: `rgb(${color.r},${color.g},${color.b})` }
    },
    styleItem (x) {
      return x
    },
    async fetch () {
      const ftd_users = await this.get({ collection: 'screen', query: { type: 'ftd', created_on: this.dates.join('|')  } })
      const retention_users = await this.get({ collection: 'screen', query: { type: 'rtn', created_on: this.dates.join('|')  } })
      this.top_users = [/*ftd*/ftd_users.data,/*rtn*/retention_users.data]
   
    },
    
  },
  destroyed() {
    if (this.timers.interval)
      clearInterval(this.timers.interval)
  },

}
</script>

<style scoped>
.pa-p10 {
  padding: 1% 2%;
}
.top-content {
  flex: 0;
}
.bottom-content {
  flex: .6;
}
.full-height {
  height: 100%;
}
.zoom {
  transform: scale(1.05);
  z-index: 9999;
}
.zoom-out {
  transform: scale(0.98);
 
}
/deep/ .v-input{
      max-width: 325px;
}
.caption {
  font-size: .8vw !important;
}


.display-size {
  font-size: 5.5vh;
  height: 5vh;
  margin: 4vh;
  /* text-align: center; */
  
}
.deposit-title {
      font-size: 7vh;
    margin-top: 3vh;
}
.date-title {
      font-size: 6vh;
    margin-bottom: 12vh;
    margin-top: 4vh;
}
.money-title {
    font-size: 20vh;
    margin-bottom: 4vh;
    margin-top: 4vh;
}
table tr td, table tr th {
  font-size: 2.5vh;
  padding: 0.7vh;

}
#user table tr td{
  text-align: start !important;
}


.agents-header .col{
  font-size: 1.2rem;
}

.fs-2-rem {
  font-size: 2rem;

}

</style>